import React, {useEffect, useState} from 'react'
import axios from './axios';
import './Row.css';
import YouTube from "react-youtube";
import movieTrailer from "movie-trailer";

const baseUrl = "https://image.tmdb.org/t/p/original/";

function Row({title, fetchUrl, isLarge}) {
    const [movies, setMovies] = useState([]);
    const [trailerUrl, setTrailerUrl] = useState("");
    const [movie, setMovie] = useState();
    const [isOn, set] = useState(false);
    

    useEffect(() => {
        async function fetchData() {
            const request = await axios.get(fetchUrl)
            setMovies(request.data.results);
            return request;
        }
        fetchData();

    }, [fetchUrl]);

    const opts = {
        height: "390",
        width: "100%",
        playerVars : {
            autoplay: 1
        }
    };

    // const handleClick = (movie) => {
    //     if(trailerUrl) {
    //         setTrailerUrl('');
    //     } else {
    //         movieTrailer(movie?.title || movie?.name || movie?.original_name || "").
    //         then(url => {
    //             const URLsearch = new URLSearchParams(new URL(url).search);
    //             setTrailerUrl(URLsearch.get('v'));
    //         }).
    //         catch(error => {
    //             console.log(error);
    //         })
    //     }
    // }

    const handleClick = (movie) => {
        setMovie(movie);        
        if(isOn) {
            set(false);
        } else {
            set(true);
        }
        console.log(movie);
    }


  return (
    <div className='row'>
        <h2 className={`'row_title' ${isLarge && 'row_title_Large'}`}> {title} </h2>
        
        <div className='row_posters'>
            {movies.map(movie => (
                <img className={`row_poster ${isLarge && 'row_poster_Large'}`}
                key={movie.id}
                onClick = {() => handleClick(movie)}
                src={`${baseUrl}${isLarge ? movie.poster_path : movie.backdrop_path}`} alt={movie.name} ></img>
            ))}
        </div>
        
        
        {isOn &&  
            <div className='row_poster_info'>
                <div className='row_poster_typo'>
                    <h3 className='info_title'>{movie?.title || movie?.name || movie?.original_name}</h3>
                    <p className='info_rating'>Rating: {movie.vote_average}/10 <img className='imdb_logo' src='https://m.media-amazon.com/images/G/01/IMDb/BG_rectangle._CB1509060989_SY230_SX307_AL_.png' alt='imdb logo'/></p> 


                    <h4 className='info_overview'>Overview:</h4>
                    <p className='info_desc'>{movie.overview}</p>
                </div>

                <div className='row_poster_img'>
                    <img className='info_image' src={`${baseUrl}${movie.poster_path}`} alt={movie.name} />
                </div>
            </div>
        }

    </div>
  )
}

export default Row